<template>
    <div class="container">
        <div class="main__info" v-if="users">
            <div class="main__info-item" v-for="user in users" :key="user.id">
                <h2>{{ user.id }}</h2>
                <p>{{ user.name }}</p>
                <p>{{ user.username }}</p>
                <p>{{ user.email }}</p>   
            </div>
        </div>
        <div v-else>
            Loading....
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
               
            }
        },
        created() {
           this.getUsers()  
        },
        computed: {
            users() {
                return this.$store.state.users
            }
        },
        methods: {
            getUsers() {
                this.$store.dispatch('getUsers')
            }
           
        },
    }
</script>

